import { Component, OnInit} from '@angular/core';
import { FormBuilder, FormGroup, Validators} from '@angular/forms';
import { Location } from '@angular/common';
import { Router } from '@angular/router';

@Component({
  selector: 'app-lca-initiation',
  templateUrl: './lca-initiation.component.html',
  styleUrls: ['./lca-initiation.component.css']
})
export class LcaInitiationComponent implements OnInit {

  isLinear = false;
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  radioItems = [{code: 'one', name: 'For Self'}, {code: 'two', name: 'For Others'}];
  rule = { mode: true };
  reqType: any;
  foods = [
    {value: 'steak-0', viewValue: 'Steak'},
    {value: 'pizza-1', viewValue: 'Pizza'},
    {value: 'tacos-2', viewValue: 'Tacos'}
  ];
  permitType= [{value: '0', viewValue: 'Work Permit Processing'},
  {value: '1', viewValue: 'Extension/Amendment/Transfer'},
  {value: '2', viewValue: 'Others'}];
  states= ['Boston' , 'Oston' , 'Toston'];
  cities= [ 'New York', 'California', 'Los Angles']
  screenHint = `Lorem ipsum dolor sit amet
  consectetur adipisicing elit. Deserunt voluptate dolores saepe non, cumque ad officia alias distinctio
  quibusdam laboriosam nihil quos eius minus aut tempore repellendus rem tempora ducimus! Lorem ipsum
  dolor sit amet consectetur adipisicing elit. Ut sunt iusto ipsum nobis commodi similique fuga earum?
  Alias iste dolorum, aspernatur impedit esse aliquid. Recusandae nihil exercitationem deleniti mollitia
  veniam`;
  public applications= [ {selected: 'true', reqNo: 'IN-329296', desCountry: 'USA'} ,
   {selected: 'true', reqNo: 'IN-329298', desCountry: 'USA'}];
   public req;
   public searchResult= false;
   public resultObject: any;
  constructor(
    private _formBuilder: FormBuilder,
    private _location: Location,
    private _router: Router
  ) { }

  ngOnInit() {
    this.firstFormGroup = this._formBuilder.group({
      assignmentLoaction: ['', Validators.required],
      assignmentFor: ['', Validators.required],
      wiproDivision: ['', Validators.required],
      sourceCountry: ['', Validators.required],
      destCountry: ['', Validators.required],
      visaType: ['', Validators.required],
      startDate: ['', Validators.required],
      endDate: ['', Validators.required],
      assignmentAddress: ['', Validators.required],
      imgRole: ['', Validators.required],
      city: ['', Validators.required],
      state: ['', Validators.required],
      search: ['']
    });
    this.secondFormGroup = this._formBuilder.group({
      secondCtrl: ['', Validators.required]
    });
  }

  back() {
    this._router.navigate(['/WT_GIMS_E']);
  }
  setreqType(value) {
    console.log('req is ', value);
    this.req = value.code;
  }
  showSearch() {
  if (this.req === 'two') {
    return true;
  }
}
getPerson() {
   this.resultObject = {
    name: 'Anis Alam',
    id: 329296,
    manager: 'Varun Singh'
  };
  this.searchResult = true;
}
}



